<template>
<div v-theme:column="'wide'" id="Show-Blog">
  <h1>博客总览</h1>
  <input type="text" placeholder="搜索" v-model="search">
  <div class="single-blog" v-for='blog in filteredBlogs' :key='blog.title'>
    <router-link v-bind:to="'/blog/'+blog.id">
      <h2 v-rainbow>{{blog.title | toUppercase}}</h2>
    </router-link>
    <article>
      {{blog.content | snippet}}
    </article>
  </div>
</div>
</template>

<script>
import axios from 'axios'


export default {
  name: 'Show-Blog',
  data() {
    return {
      blogs: [],
      search: ''
    }
  },
  created() {
    // this.$http.get('https://wd7807406915vbkkrj.wilddogio.com/posts')

    axios.get('/posts.json')
      .then((data) => {
        return data.data;
      })
      .then((data) => {
        var blogsArray = [];
        for (let key in data) {
          // console.log(data[key])
          data[key].id = key;
          blogsArray.push(data[key]);
        }
        this.blogs = blogsArray;

      })
  },
  computed: {
    filteredBlogs: function() {
      return this.blogs.filter((blog) => {
        return blog.title.match(this.search);
      })
    }
  },
  filters: {
    toUppercase(value) {
      return value.toUpperCase();
    },
    snippet(value) {
      return value.slice(0, 100);
    }

  },
  directives: {
    'rainbow': {
      bind(el, binding, vnode) {
        el.style.color = '#' + Math.random().toString(16).slice(2, 8);
      }
    },
    'theme': {
      bind(el, binding, vonde) {
        if (binding.value == 'wide') {
          el.style.maxWidth = '1260px';
        } else if (binding.value == 'narrow') {
          el.style.maxWidth = '560px';
        }

        if (binding.arg == 'column') {
          el.style.background = '#' + Math.random().toString(16).slice(2, 8);
          el.style.padding = '20px';
        }
      }
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
#Show-Blog {
  max-width: 800px;
  margin: 0 auto;
}

.single-blog {
  padding: 20px;
  margin: 20px 0;
  box-sizing: border-box;
  background: #eee;
  border: 1px dotted #aaa;
}

#Show-Blog a {
  text-decoration: none;
  color: #444;
}

input {
  padding: 8px;
  width: 100%;
  box-sizing: border-box;
  border-radius: 6px;
  border: 0;
  outline: none;
  cursor: pointer;
}

h2 {
  text-align: center;
  display: -webkit-box;
  -webkit-box-orient: vertical;
  -webkit-line-clamp: 2;
  overflow: hidden;
  text-align: center;
  font-size: 24px;
}

article {
  display: -webkit-box;
  -webkit-box-orient: vertical;
  -webkit-line-clamp: 1;
  overflow: hidden;
  text-align: center;
  font-size: 16px;
}
</style>



