<template>
<div id="SingleBlog">
  <h1>{{blog.title}}</h1>
  <article>
    {{blog.content}}
  </article>
  <span class="zz">作者:</span>
  <span class="liyouxin">{{blog.author}}</span>

  <br>
  <br>


  <button type="button" name="button" @click="delSingleBlog()">删除</button>
  <router-link :to="'/EditBlog/'+id" tag="button">编辑</router-link>
</div>
</template>

<script>
import axios from 'axios'


export default {
  name: 'SingleBlog',
  data() {
    return {
      blog: {},
      id: this.$route.params.id
    }
  },
  created() {
    axios.get('/posts/' + this.id + ".json")
      .then((data) => {
        // return data.data;
        this.blog = data.data;
        // console.log(data)
      })
  },
  methods: {
    delSingleBlog() {
      axios.delete('/posts/' + this.id + ".json")
        .then(pesponse => {
          this.$router.push({
            path: '/'
          })
        })
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
#SingleBlog {
  max-width: 960px;
  margin: 0 auto;
  padding: 20px;
  background: #eee;
  border: 1px dotted #aaa;
  text-align: left;
}

.fl {
  text-indent: 10px;
}

button {
  border-radius: 6px;
  border: 0;
  width: 60px;
  height: 30px;
  cursor: pointer;
  background: #FF554E;
  color: #fff;

}

article {
  margin-bottom: 30px;
  white-space: pre-wrap
}

h1 {
  text-align: center;
  font-size: 22px;
}

.liyouxin {
  color: #FF554E;
}

.zz {
  margin-top: 20px;
}
</style>



