<template>
<div id="add-blog">

  <h2>添加博客</h2>

  <form v-if='!submmited'>

    <label>博客标题</label>

    <input type="text" v-model="blog.title" required />

    <label>博客内容</label>

    <textarea v-model="blog.content"></textarea>

    <label>作者:</label>

    <input type="text" v-model="blog.author">

    <button type="button" @click="post">添加博客</button>

  </form>

  <div class="">

    <h3 v-if='submmited'>您的博客发布成功</h3>

  </div>


  <div id='preview'>

    <h3>博客总览</h3>

    <p>博客标题:</p>

    <p class="blog-content">{{blog.title}}</p>

    <p>博客内容:</p>

    <p class="blog-content">{{blog.content}}</p>

    <p>作者:</p>

    <p class="blog-content liyouxin">{{blog.author}}</p>

  </div>

</div>
</template>

<script>
import axios from 'axios'

export default {
  name: 'add-blog',
  data() {
    return {
      blog: {
        title: '',
        content: '',
        categories: [],
        author: ''
      },
      submmited: false
    }
  },
  methods: {
    // post: function() {
    //   this.$http.post('https://wd7807406915vbkkrj.wilddogio.com/posts.json', {
    //     title: this.blog.title,
    //     body: this.blog.content,
    //     userId: 1
    //   }).then(function(data) {
    //     console.log(data);
    //     this.submmited = true;
    //   })
    // }

    post: function() {
      axios.post('/posts.json', this.blog)
        .then((data) => {
          this.submmited = true;
        });
    }

  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
#add-blog * {
  box-sizing: border-box;
}

#add-blog {
  margin: 20px auto;
  max-width: 600px;
  padding: 20px;
  color: #FF554E;
}

label {
  display: block;
  margin: 20px 0 10px;
}

input[type='text'],
textarea,
select {
  width: 100%;
  display: block;
  padding: 8px;
  padding-left: 15px;
  border: 1px solid #a7a7a7;

}

textarea {
  height: 200px;
}

#checkboxs label {
  display: inline-block;
  margin-top: 0;
}

#checkboxs input {
  display: inline-block;
  margin-right: 10px;
}

button {
  display: block;
  margin: 20px 0;
  background: crimson;
  color: #fff;
  border: 0;
  padding: 14px;
  border-radius: 4px;
  font-size: 18px;
  cursor: pointer;
}

#preview {
  padding: 10px 20px;
  border: 1px dotted #ccc;
  margin: 30px 0;
}

#preview h3,
#preview p {
  margin: 40px 0;
}

h3 {
  margin-top: 10px;
}

ul {
  list-style: none;
}

ul>li {
  text-indent: 35px;
  margin-bottom: 15px;
  display: inline-block;
}

#preview .blog-content {
  text-align: center;

}

.liyouxin {
  color: #FF554E;
}
</style>



